import React, { useState } from "react";
import { X, Search } from "lucide-react";
import { toast } from 'react-toastify'
import Toast from "../auth/Toast";
import axios from "axios";
import { title } from "process";
import Image from "next/image";
import avtar from '../../../public/assets/avtar.jpg'
import { useParams } from "next/navigation";
interface AddMilestoneProps {
  onClose: () => void;
  teamMembers: Array<{
    _id: string;
    fullName: string;
    role: string;
    avatar: string;
  }>;
  setmileStone: React.Dispatch<React.SetStateAction<Milestone[]>>;
  fromOverview: boolean;
}

interface Milestone {
  _id: number;
  description: string;
  assignedTo: string;
  dueDate: string;
}


const AddMilestone: React.FC<AddMilestoneProps> = ({
  onClose,
  teamMembers,
  setmileStone,
  fromOverview,
}) => {
  const [milestoneDescription, setMilestoneDescription] = useState("");
  const [selectedMember, setSelectedMember] = useState<string[]>([]);
  const [dueDate, setDueDate] = useState("");
  const [searchQuery, setSearchQuery] = useState(""); // State for search query

  // Filter team members based on the search query

  const filteredMembers = 
    teamMembers.filter(member => member?.fullName?.toLowerCase().includes(searchQuery?.toLowerCase() || ""));


  const params = useParams();
    const { id } = params;
    // console.log(id,milestoneDescription, dueDate )
  const AddMilestoneFromOverview = async () => {
  // Remove the setTimeout for validation toast
  if (!milestoneDescription || selectedMember?.length === 0 || !dueDate) {
    toast.error("Please fill in all fields.");
    return;
  }

  const projectData = {
    projectId: id,
    title: milestoneDescription,
    assignedTo: teamMembers
      .filter((member) => selectedMember.includes(member.fullName))
      .map((member) => member._id) || [], 
    dueDate: dueDate,
    status: "pending"
  };

  try {
    const response = await axios.post(
      `${process.env.NEXT_PUBLIC_API_URL}/projects/project/add-milestone`,
      projectData,
      {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
      }
    );
    if (response.status === 200) {
      toast.success("Milestone added successfully");
      setTimeout(() => {
        onClose();
      }, 2000);
    }
  } catch (error) {
    console.error("Failed to create project:", error);
    toast.error("Failed to add milestone"); // Add error toast
  }
};
  const handleAddMilestone = () => {
    if(fromOverview){
      AddMilestoneFromOverview()
      return ;
    }
    else{
    if (!milestoneDescription || selectedMember?.length === 0 || !dueDate) {
      toast.error("Please fill in all fields.");
      return;
    }
  }

    const newMilestone: Milestone = {
      _id: Date.now(), // Unique ID for the milestone
      description: milestoneDescription,
      assignedTo: selectedMember.join(' '),
      dueDate: dueDate,
    };

    setmileStone((prevMilestones) => [...prevMilestones, newMilestone]); // Add to the milestone array
    onClose(); // Close the modal
  };
  const handleSelect = (memberName: string) => {
    if (selectedMember) {
      setSelectedMember((prev) =>
        prev?.includes(memberName)
          ? prev.filter((member) => member !== memberName)  // Remove if already selected
          : [...(prev || []), memberName] // Add if not selected
      );
    }


  }
  return (
    <>
    {console.log(teamMembers)}
      <Toast />
      <div className="fixed inset-0 flex items-center justify-center bg-black/50 z-50">
        <div className="bg-white rounded-lg shadow-lg w-[400px]">
          <div className="p-4">
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-lg font-medium text-gray-900">Add Milestone</h2>
              <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
                <X className="w-5 h-5" />
              </button>
            </div>

            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1.5">
                  Project Milestone
                </label>
                <input
                  type="text"
                  placeholder="Enter project milestone..."
                  className="w-full px-3 py-2 bg-white border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-1 focus:ring-indigo-500"
                  value={milestoneDescription}
                  onChange={(e) => setMilestoneDescription(e.target.value)}
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1.5">
                  Assigned to
                </label>
                <div className="relative mb-2">
                  <div className="absolute inset-y-0 left-3 flex items-center pointer-events-none">
                    <Search className="w-4 h-4 text-gray-400" />
                  </div>
                  <input
                    type="text"
                    placeholder="Search team member"
                    className="w-full pl-10 pr-3 py-2 bg-white border border-gray-200 rounded-lg text-sm"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)} // Update search query on input change
                  />
                </div>

                <div className="flex flex-wrap gap-1.5 max-h-32 overflow-y-auto">
                  {filteredMembers.slice(0, 5).map((member) => (
                    <div
                      key={member._id}
                      onClick={() => handleSelect(member.fullName)}
                      className={`relative flex items-center gap-2 px-2.5 py-1 rounded-full border cursor-pointer transition-colors ${selectedMember.includes(member.fullName)
                          ? "bg-indigo-50 border-indigo-200"
                          : "bg-white border-gray-200 hover:bg-gray-50"
                        }`}
                    >
                      {/* Green tick badge */}
                      {selectedMember.includes(member.fullName) && (
                        <div className="absolute -top-0.75 -right-1 bg-green-500 text-white text-xs rounded-full w-4 h-4 flex items-center justify-center">
                          âœ”
                        </div>
                      )}

                      <Image
                        src={avtar}
                        alt={member.fullName}
                        className="w-5 h-5 rounded-full"
                      />
                      <div className="text-xs">
                        <p className="font-medium text-gray-900">{member.fullName}</p>
                        <p className="text-gray-500">({member.role})</p>
                      </div>
                    </div>
                  ))}
                </div>

              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1.5">
                  Due Date
                </label>
                <input
                  type="date"
                  className="w-full px-3 py-2 bg-white border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-1 focus:ring-indigo-500"
                  value={dueDate}
                  onChange={(e) => setDueDate(e.target.value)}
                />
              </div>
            </div>

            <div className="mt-6 flex justify-end gap-2">
              <button
                onClick={onClose}
                className="px-4 py-1.5 bg-gray-100 text-gray-600 rounded text-sm font-medium hover:bg-gray-200"
              >
                Cancel
              </button>
              <button
                onClick={handleAddMilestone}
                className="px-4 py-1.5 bg-[#5D56BD] text-white rounded text-sm font-medium hover:bg-[#4A44A7]"
              >
                Add Milestone
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default AddMilestone;
