import React, { useState, useEffect } from "react";
import { X } from "lucide-react";
import Image from "next/image";
import axios from "axios";

interface Member {
  _id: string;
  fullName: string;
  role: string;
  position: string;
  avatar: string;
}

interface AddTeamMembersProps {
  onClose: () => void;
  onAddMember: (member: Member) => void;
  teamMembers: Member[];
}

const AddTeamMembers: React.FC<AddTeamMembersProps> = ({
  onClose,
  onAddMember,
  teamMembers,
}) => {
  // Single source of truth for selected members
  const [selectedMembers, setSelectedMembers] = useState<Member[]>(teamMembers);
  const [searchResults, setSearchResults] = useState<Member[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [showResults, setShowResults] = useState(false);

  // Debounced search
  useEffect(() => {
    const searchUsers = async () => {
      if (!searchTerm.trim()) {
        setSearchResults([]);
        return;
      }

      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/users/search?name=${searchTerm}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );

        // Filter out already selected members
        const filteredResults = response.data.filter(
          (member: Member) => 
            !selectedMembers.some(selected => selected._id === member._id)
        );
        
        setSearchResults(filteredResults);
        setShowResults(true);
      } catch (error) {
        console.error("Error searching users:", error);
        setSearchResults([]);
      }
    };

    const timeoutId = setTimeout(searchUsers, 500);
    return () => clearTimeout(timeoutId);
  }, [searchTerm, selectedMembers]);

  const handleSelectMember = (member: Member) => {
    setSelectedMembers(prev => {
      // Check if member already exists
      if (prev.some(m => m._id === member._id)) {
        return prev;
      }
      return [...prev, { ...member, role: "" }];
    });
    setSearchTerm("");
    setShowResults(false);
  };

  const handleRemoveMember = (memberId: string) => {
    setSelectedMembers(prev => prev.filter(member => member._id !== memberId));
  };

  const handleRoleChange = (memberId: string, newRole: string) => {
    setSelectedMembers(prev => 
      prev.map(member => {
        if (member._id === memberId) {
          const updatedMember = { ...member, role: newRole };
          onAddMember(updatedMember); // Notify parent component
          return updatedMember;
        }
        return member;
      })
    );
  };

  return (
    <div className="fixed inset-0 flex items-center justify-center bg-black/50">
      <div className="bg-white rounded-xl h-[500px] shadow-lg w-[480px] p-5 relative">
        {/* Header */}
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold">Add Team Members</h2>
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
            <X className="w-5 h-5" />
          </button>
        </div>

        {/* Search Input */}
        <div className="flex gap-2 mb-4">
          <div className="flex-1 p-2 border-2 rounded-lg min-h-[40px]">
            {selectedMembers.map(member => (
              <div key={member._id} className="inline-flex items-center gap-2 bg-[#5D56BD] rounded-full px-2 py-1 m-1">
                <Image
                  src={member.avatar || "/placeholder.jpg"}
                  alt=""
                  width={20}
                  height={20}
                  className="rounded-full"
                />
                <span className="text-xs text-white">{member.fullName}</span>
                <button
                  onClick={() => handleRemoveMember(member._id)}
                  className="text-white hover:text-red-200"
                >
                  <X className="w-4 h-4" />
                </button>
              </div>
            ))}
            <input
              type="text"
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
              className="border-none outline-none px-2 py-1 min-w-[120px]"
              placeholder="Search members..."
            />
          </div>
        </div>

        {/* Search Results */}
        {showResults && searchResults.length > 0 && (
          <div className="absolute z-10 w-[calc(100%-40px)] bg-white border rounded-lg shadow-lg">
            {searchResults.map(result => (
              <div
                key={result._id}
                onClick={() => handleSelectMember(result)}
                className="flex items-center gap-2 p-2 hover:bg-gray-50 cursor-pointer"
              >
                <Image
                  src={result.avatar || "/placeholder.jpg"}
                  alt=""
                  width={32}
                  height={32}
                  className="rounded-full"
                />
                <span>{result.fullName}</span>
              </div>
            ))}
          </div>
        )}

        {/* Member List */}
        <div className="mt-4">
          <div className="flex justify-between text-sm text-gray-500 mb-2">
            <span>Team members ({selectedMembers.length})</span>
            <span>Roles</span>
          </div>

          <div className="space-y-3">
            {selectedMembers.map(member => (
              <div key={member._id} className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <Image
                    src={member.avatar || "/placeholder.jpg"}
                    alt=""
                    width={40}
                    height={40}
                    className="rounded-full"
                  />
                  <div>
                    <p className="text-sm font-medium">{member.fullName}</p>
                    <p className="text-xs text-gray-500">{member.position}</p>
                  </div>
                </div>
                <select
                  value={member.role}
                  onChange={e => handleRoleChange(member._id, e.target.value)}
                  className="px-3 py-1 text-sm border rounded-lg"
                >
                  <option value="">Select Role</option>
                  <option value="owner">Owner</option>
                  <option value="admin">Admin</option>
                  <option value="user">User</option>
                  <option value="client">Client</option>
                </select>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddTeamMembers;