import { useState } from "react";
import { X, Plus, Camera, Pencil } from "lucide-react";
import AddTeamMembers from "./AddTeamMembers";
import AddMilestone from "./AddMilestone";
import axios from "axios";
import Toast from "../auth/Toast";
import { toast } from "react-toastify";
import avtar from '../../../public/assets/avtar.jpg'

import Image from 'next/image'
interface TeamMember {
  _id: string;
  fullName: string;
  role: string;
  position: string;
  avatar: string;
}

interface Milestone {
  _id: number;
  description: string;
  assignedTo: string;
  dueDate: string;
}

interface CreateNewProjectProps {
  onClose: () => void;
}

const CreateNewProject: React.FC<CreateNewProjectProps> = ({ onClose }) => {
  const [projectName, setProjectName] = useState("");
  const [projectCategory, setProjectCategory] = useState("");
  const [aboutProject, setAboutProject] = useState("");
  const [collaboration, setCollaboration] = useState("");
  const [teamMembers, setTeamMembers] = useState<TeamMember[]>([]);

  const handleAddMember = (newMember: TeamMember) => {
    setTeamMembers((prev) => {
      const extingIndex = prev.findIndex(
        (member) => member._id === newMember._id
      );
      if (extingIndex !== -1) {
        if (newMember.role === "Select Role") {
          toast.error("Please select a valid role");
        }
        const updatedList = [...prev];
        updatedList[extingIndex] = {
          ...updatedList[extingIndex],
          role: newMember.role,
        };
        return updatedList;
      }
      return [...prev, newMember];
    });
    // setTeamMembers((prev)=> [...prev, newMember])
  };
  const [milestones, setmileStone] = useState<Milestone[]>([]);
  const [isAddTeamMemberModalOpen, setAddTeamMemberModalOpen] = useState(false);
  const [isAddMilestone, setAddMilestone] = useState(false);

  const handleClick = async () => {
    // const projectData = {
    //   projectName,
    //   projectCategory,
    //   aboutProject,
    //   collaboration,
    //   teamMembers,
    //   milestones,
    // };
    if (!projectName || !projectCategory || !aboutProject || !collaboration || teamMembers.length === 0) {
      toast.error("Please fill all required fields and add at least one team member.");
      return;
    }
    const projectData = {
      name: projectName,
      category: projectCategory,
      description: aboutProject,
      collaboration,
      members: teamMembers.map((member) => ({
        id: member._id,
        name: member.fullName,
        role: member.role,
      })),
      milestones: milestones.map((milestone) => ({
        title: milestone.description,
        assignedTo:
          teamMembers.find((member) => member.fullName === milestone.assignedTo)
            ?._id || null,
        dueDate: milestone.dueDate,
      })),
      private: false, // Set any default values as needed
    };

    try {
      const response = await axios.post(
        `${process.env.NEXT_PUBLIC_API_URL}/projects/create`,
        projectData,
        {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        }
      );
      if (response.status == 201) {
        toast.success("Project created successfully");
        // console.log("Project created successfully:", response.data.project);
        setTimeout(() => {
          onClose(); // Close the modal after successful creation
        }, 3000);
      }
    } catch (error) {
      console.error("Failed to create project:", error);
    }

    console.log("Project Data:", projectData);
  };
  
  return (
    <>
      <Toast />

      <div className="fixed inset-0 flex items-center justify-center bg-black/50 z-50">
        <div className="bg-white rounded-lg w-[1000px]">
          {/* Header */}
          <div className="flex justify-between items-center px-6 py-4 border-b">
            <h2 className="text-lg font-semibold">Create New Project</h2>
            <button onClick={onClose}>
              <X className="w-5 h-5 text-gray-500" />
            </button>
          </div>

          {/* Content */}
          <div className="p-6">
            <div className="grid grid-cols-2 gap-6">
              {/* Left Column */}
              <div className="space-y-4">
                <div className="flex flex-col items-center">
                  <div className="relative mb-1">
                    <div className="w-16 h-16 bg-gray-50 rounded-full flex items-center justify-center border border-gray-200">
                      <Camera className="w-6 h-6 text-gray-400" />
                    </div>
                    <button className="absolute bottom-0 right-0 w-6 h-6 bg-indigo-600 rounded-full flex items-center justify-center">
                      <Pencil className="w-3 h-3 text-white" />
                    </button>
                  </div>
                  <span className="text-xs text-gray-500">Project Image</span>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-xs text-gray-600 mb-1">
                      Project Name
                    </label>
                    <input
                      type="text"
                      placeholder="Enter project name"
                      className="w-full h-8 px-2 rounded border border-gray-200 text-sm"
                      value={projectName}
                      onChange={(e) => setProjectName(e.target.value)}
                    />
                  </div>
                  <div>
                    <label className="block text-xs text-gray-600 mb-1">
                      Project Category
                    </label>
                    <select
                      className="w-full h-8 px-2 rounded border border-gray-200 text-sm"
                      value={projectCategory}
                      onChange={(e) => setProjectCategory(e.target.value)}
                    >
                      <option value="" disabled hidden>
                        Select Category
                      </option>
                      <option value="Product">Product Development</option>
                      <option value="Customer">
                        Customer Relation Manager(CRM)
                      </option>
                      <option value="Enterprise">
                        Enterprise Resource Planning(ERP)
                      </option>
                      <option value="AI">Artificial Intelligence(AI)</option>
                      <option value="DevOps">DevOps</option>
                      <option value="Web Development">Web Development</option>
                      <option value="Mobile Development">
                        Mobile Development
                      </option>
                      <option value="Cybersecurity">Cybersecurity</option>
                    </select>
                  </div>
                </div>

                <div>
                  <label className="block text-xs text-gray-600 mb-1">
                    Team Members
                  </label>
                  <div className="border border-gray-200 rounded p-2 h-24 overflow-y-auto">
                    <div className="flex flex-wrap gap-1">
                      {teamMembers.map((member) => (
                        <div
                          key={member._id}
                          className="flex items-center bg-[#565DBD] text-white gap-1.5 rounded-full p-1"
                        >
                          <Image
                            src={avtar}
                            alt="Avtar"
                            className=" rounded-full"
                            width={20}
                            height={20}
                          />
                          <div className="text-xs text-[#ffffff]">
                            <p className="font-medium  leading-none">
                              {member.fullName}
                            </p>
                            <p className=" text-[10px] leading-tight">
                              {member.role}
                            </p>
                          </div>
                          <button
                            onClick={() =>
                              setTeamMembers((prev) =>
                                prev.filter((tm) => tm._id != member._id)
                              )
                            }
                            className=" hover:text-white hover:bg-[#FF0000]"
                          >
                            <X className="w-4 h-4" />
                          </button>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="flex justify-center pt-4">
                    <button
                      type="button"
                      className="px-4 py-2.5 rounded-lg bg-[#5D56BD] text-white hover:bg-[#4A44A7] text-sm flex items-center gap-2"
                      onClick={() => setAddTeamMemberModalOpen(true)}
                    >
                      <Plus className="w-3 h-3" /> Add Member
                    </button>
                  </div>
                </div>
              </div>

              {/* Right Column */}
              <div className="space-y-4">
                <div>
                  <label className="block text-xs text-gray-600 mb-1">
                    About Project
                  </label>
                  <textarea
                    placeholder="Describe..."
                    rows={3}
                    className="w-full px-2 py-1.5 rounded border border-gray-200 text-sm resize-none"
                    value={aboutProject}
                    onChange={(e) => setAboutProject(e.target.value)}
                  />
                </div>

                <div>
                  <label className="block text-xs text-gray-600 mb-1">
                    How we collaborate?
                  </label>
                  <textarea
                    placeholder="Describe..."
                    rows={3}
                    className="w-full px-2 py-1.5 rounded border border-gray-200 text-sm resize-none"
                    value={collaboration}
                    onChange={(e) => setCollaboration(e.target.value)}
                  />
                </div>

                <div>
                  <label className="block text-xs text-gray-600 mb-1">
                    Milestones (Optional)
                  </label>
                  <div className="border border-gray-200 rounded p-2 h-24 overflow-y-auto">
                    {milestones.map((milestone) => (
                      <div
                        key={milestone._id}
                        className="bg-gray-50 p-2 rounded text-xs"
                      >
                        <p className="text-gray-900 leading-tight">
                          {milestone.description}
                        </p>
                        <div className="flex items-center gap-2 mt-1">
                          <div className="flex items-center gap-1">
                            <img
                              src="/api/placeholder/24/24"
                              alt=""
                              className="w-4 h-4 rounded-full"
                            />
                            <span className="text-gray-600">
                              {milestone.assignedTo}
                            </span>
                          </div>
                          <span className="text-gray-600">
                            Due date: {milestone.dueDate}
                          </span>
                        </div>
                      </div>
                    ))}
                  </div>
                  <div className="flex justify-center pt-2">
                    <button
                      type="button"
                      className="px-4 py-2.5 rounded-lg bg-[#5D56BD] text-white hover:bg-[#4A44A7] text-sm flex items-center gap-2"
                      onClick={() => setAddMilestone(true)}
                    >
                      <Plus className="w-4 h-4" /> Add Milestone
                    </button>
                  </div>
                  {/* <button
                  onClick={() => setAddMilestone(true)}
                  className="mt-2 w-full flex items-center justify-center gap-1 py-1.5 bg-indigo-600 text-white rounded text-xs font-medium hover:bg-indigo-700"
                >
                  <Plus className="w-3 h-3" />
                  Add Milestone
                </button> */}
                </div>
              </div>
            </div>
          </div>

          {/* Footer */}
          <div className="flex justify-end gap-2 px-6 py-4 border-t">
            <button
              className="px-4 py-1.5 bg-[#5D56BD] text-white rounded text-sm font-medium hover:bg-indigo-700"
              onClick={handleClick}
            >
              Create Project
            </button>
            <button
              onClick={onClose}
              className="px-4 py-1.5 bg-gray-100 text-gray-600 rounded text-sm font-medium hover:bg-gray-200"
            >
              Cancel
            </button>
          </div>
        </div>

        {isAddTeamMemberModalOpen && (
          <AddTeamMembers
            onClose={() => setAddTeamMemberModalOpen(false)}
            onAddMember={handleAddMember}
            teamMembers={teamMembers}
          />
        )}
        {isAddMilestone && (
          <AddMilestone
            onClose={() => setAddMilestone(false)}
            teamMembers={teamMembers}
            setmileStone={setmileStone}
            fromOverview= {false}
          />
        )}
      </div>
    </>
  );
};

export default CreateNewProject;
